@import "../../styles/screenSize.scss";

.task-board {
  width: 80vw;
  margin: auto;

  &-link {
    text-decoration: none;
    color: inherit;

    &-title {
      text-align: center;
    }
  }

  &__addtask {
    margin: 1rem 0;
    border-radius: 8px;
    width: 20rem;
    height: auto;
    background-color: rgba(47, 73, 240, 0.201);

    &-descr {
      font-size: 2rem;
      padding: .5rem 2rem;
    }

    &:hover {
      background-color: rgba(47, 73, 240, 0.509);
      cursor: pointer;
    }

    &:active {
      background-color: rgba(47, 73, 240, 0.765);
    }
  }
}

.tb-modal {
  &-input {
    margin: 2rem 1rem;
    width: 40rem;
    height: 5rem;
    border: 1px solid rgba(153, 152, 152, 0.648);
    padding: 1rem;
  }

  &-button {
    margin: 5rem 2rem;
    width: 40rem;
    height: 5rem;
  }
}

@media (max-width: $bigMobWidth) {
  .task-board {
    width: 90vw;
  }

  .tb-modal {
    display: grid;
    grid-template-rows: repeat(5, 7vh);

    &-input {
      width: 40vw;
      margin: 2rem 1rem;
      height: 3.5rem;
    }

    &-button {
      width: 40vw;
      margin: 2rem 1rem;
      height: 3.5rem;
    }
  }
}